import { Box, Button, Center, Flex, Heading, Image, Link, Text } from '@chakra-ui/react'
import React from 'react'
import '../CSS/Contact.css'
import logo from '../logo.png'
import { useState, useContext } from 'react'
import { Context } from '../Pages/ContextProvider'

const Contact = () => {
  let { isValue } = useContext(Context)

  let [set, setClass] = useState('')
  let [set2, setClass2] = useState('')
  let fun = () => {
    if (window.pageYOffset > 0) {
      setClass('con')
      setClass2('summ')
    } else {
      setClass('')
      setClass2('')
    }
  }
  window.addEventListener('scroll', fun)

  
  return (
    <Box id='contact' bg={isValue === 'dark' ? 'blackAlpha.900' : 'white'}>
      <svg viewBox="0 50 500 100">
        <path d="M 0 50 C 150 150 330 -30 500 50 L 500 0 L 0 0" fill="black" opacity="0.8"></path>
        <path d="M 0 50 C 150 150 300 0 500 80 L 500 0 L 0 0" fill="black"></path>
        <path d="M -100 50 C 100 150 0 0 500 100 L 500 0 L 0 0" fill="black" opacity="0.5"></path>
      </svg>
      <Center>
        <Flex
          direction={'column'}
          mt={{ base: '', md: '-30px', lg: "-160px" }}
          gap="80px" pb="60px">
          <Center>
            <Heading
              color={isValue === 'dark' ? 'white' : 'blackAlpha.900'}>
              Contact Me
            </Heading>
          </Center>
          <Center>
            <Flex
              direction={{ base: 'column', md: 'column', lg: "row" }}
              gap={{ base: '60px', md: '70px', lg: '150px' }}
              w={{ base: '100%', md: '80%', lg: '70%' }}
              alignItems={'center'}>
              <Flex
                w={{ base: '90%', md: '80%', lg: '40%' }}
                className={set}
                justify={{ base: 'center', md: 'cetner', lg: '' }}>
                <Image
                  src='https://i.pinimg.com/originals/fb/dd/3a/fbdd3a3a214c0e712bb604dea3b3b1e5.gif'
                  w={{ base: '200px', md: '300px', lg: '300px' }}
                  h={{ base: '200px', md: '300px', lg: '300px' }} />
              </Flex>
              <Flex
                direction={'column'}
                gap='10px' w={{ base: '90%', md: '80%', lg: '60%' }}
                className={set2}>
                <Heading
                  fontSize={'40px'}
                  color={isValue === 'dark' ? 'white' : 'blackAlpha.900'}
                  textAlign={{ base: 'center', md: 'cetner', lg: 'start' }}>
                  Get in touch
                </Heading>
                <Text
                  textAlign={{ base: 'center', md: 'cetner', lg: 'start' }}
                  fontWeight={'bold'}
                  fontSize="20px"
                  fontStyle={'italic'} color={isValue === 'dark' ? 'white' : 'blackAlpha.900'}>
                  You can contact me at the places mentioned below. I will try to get back to you as soon as I can.
                </Text>
                {/* social media connection  */}
                <Flex
                  gap='20px'
                  justify={{ base: 'center', md: 'cetner', lg: 'start' }}>
                  <Link
                    id='contact-github'
                    href="https://github.com/ErGopiVishwakarma"
                    target='_black'>
                    <Image
                      className='contact-github'
                      src='https://github.githubassets.com/images/modules/logos_page/GitHub-Mark.png' />
                  </Link>
                  <Link
                    id='contact-linkedin'
                    href="https://www.linkedin.com/in/gopi-vishwakarma-95b325250/"
                    target='_black'>
                    <Image
                      className='contact-linkedin'
                      src='https://w7.pngwing.com/pngs/593/197/png-transparent-in-signage-computer-icons-linkedin-desktop-social-media-linked-in-text-trademark-logo-thumbnail.png' />
                  </Link>
                  <Link
                    id='contact-email'
                    href='g2468vi@gmail.com'
                    target='_black'>
                    <Image
                      className='contact-email'
                      bg={isValue === 'dark' ? 'white' : ''} src='https://cdn-icons-png.flaticon.com/512/6244/6244438.png' />
                  </Link>
                </Flex>
                <Flex
                  justify={{ base: 'center', md: 'cetner', lg: 'start' }}>
                  <Link href='GopiVishwakarma.pdf' download>
                    <Button
                      className="button" bg="red.500"
                      colorScheme="white"
                      textStyle={'none'}
                      id="resume-button-2" >
                      Resume
                    </Button>
                  </Link>
                </Flex>

                <Heading
                  className="contact-phone"
                  textAlign={{ base: 'center', md: 'cetner', lg: 'start' }}
                  fontSize={{ base: '18px', md: '20px' }}
                  pt="10px"
                  color={isValue === 'dark' ? 'white' : 'blackAlpha.900'} >
                  Mobile No. +91 <span id="contact-phone">8303167926</span>

                </Heading>

              </Flex>
            </Flex>
          </Center>
        </Flex>
      </Center>

    </Box>
  )
}

export default Contact