import {
  Box, Flex, Button, Text, IconButton, Stack, Image, Collapse, Icon, useColorModeValue, useBreakpointValue, useDisclosure, Link
} from '@chakra-ui/react';
import { HamburgerIcon, CloseIcon, ChevronDownIcon, ChevronRightIcon, MoonIcon, SunIcon } from '@chakra-ui/icons';
import { NavHashLink } from 'react-router-hash-link';
import '../CSS/Navbar.css'
import { useContext } from 'react'
import { Context } from '../Pages/ContextProvider'
let count = 0
let value = ''
let darkFun
export const Navbar = () => {
  let { isValue, darkModeFun } = useContext(Context)
  darkFun = darkModeFun
  value = isValue
  console.log(isValue)
  console.log(darkModeFun)
  const { isOpen, onToggle } = useDisclosure();

  return (
    <Box bg="blackAlpha.900" position="fixed" zIndex="999"  w="100%" h="70px" className="nav" id="nav-menu">
      <Flex bg={useColorModeValue()} color={useColorModeValue()} minH={'60px'} py={{ base: 2 }} px={{ base: 4 }} alignItems={'center'}>

        <Flex flex={{ base: 1 }} justify={{ base: 'left', md: 'space-between' }}>
          <NavHashLink smooth to='#home' className='nav-link home'>
            <Text
              textAlign={useBreakpointValue({ base: 'left', md: 'left' })}
              fontSize={{ base: '20px', md: '25px' }}
              fontStyle="italic"
              color={useColorModeValue('white', 'white')}>
              Gopi vishwakarma
            </Text>
          </NavHashLink>
          <Flex display={{ base: 'none', md: 'flex' }} ml={10}>
            <DesktopNav />
          </Flex>
        </Flex>
        <Flex flex={{ base: 1, md: 'auto' }} justify={{ base: 'right' }} ml={{ base: -2 }} display={{ base: 'flex', md: 'none' }}>
          <IconButton
            onClick={onToggle}
            icon={
              isOpen ? <CloseIcon w={5} h={5} color="white" /> : <HamburgerIcon w={7} h={7} color="white" />
            }
            variant={'ghost'}
            aria-label={'Toggle Navigation'}
          />
        </Flex>


      </Flex>
      <Collapse in={isOpen} animateOpacity>
        <MobileNav />
      </Collapse>
    </Box>
  );
}

const DesktopNav = () => {
  const linkColor = useColorModeValue('white', 'white');
  const linkHoverColor = useColorModeValue('white', 'white');

  return (
    <Flex direction={'row'} gap={10} alignItems="center" fontStyle="italic" className="nav">



      {NAV_ITEMS.map((navItem, ind) => (
        <Box key={++count} color="white" className='navChild'>
          <NavHashLink
            smooth
            to={navItem.href}
            className={navItem.clss}
          >
            {navItem.label}

          </NavHashLink>
        </Box>
      ))}
      <Link color="white" className='nav-link resume' id="resume-button-1">
        Resume
      </Link>
      <Button onClick={() => darkFun()} >
        {value === 'light' ? <MoonIcon /> : <SunIcon />}
      </Button>
    </Flex>
  );
};



const MobileNav = () => {
  return (
    <Stack
      bg={useColorModeValue('white', 'gray.800')}
      p={4}
      display={{ md: 'none' }}
      zIndex='999'>
      {NAV_ITEMS.map((navItem) => (
        <MobileNavItem key={navItem.label} {...navItem} />
      ))}

    </Stack>
  );
};

const MobileNavItem = ({ label, children, href }) => {
  const { isOpen, onToggle } = useDisclosure();

  return (
    <Stack spacing={4} onClick={children && onToggle}>
      <Flex
        py={2}
        as={Link}
        href={href ?? '#'}
        justify={'center'}
        align={'center'}
        _hover={{
          textDecoration: 'none',
        }}>
        <Text
          fontWeight={600}
          color={useColorModeValue('gray.600', 'gray.200')}>
          {label}
        </Text>
        {children && (
          <Icon
            as={<ChevronDownIcon />}
            transition={'all .25s ease-in-out'}
            transform={isOpen ? 'rotate(180deg)' : ''}
            w={6}
            h={6}
          />
        )}
      </Flex>

      <Collapse in={isOpen} animateOpacity style={{ marginTop: '0!important' }}>
        <Stack
          mt={2}
          pl={4}
          borderLeft={1}
          borderStyle={'solid'}
          borderColor={useColorModeValue('gray.200', 'gray.700')}
          align={'start'}>
          {children &&
            children.map((child) => (
              <Link key={child.label} py={2} href={child.href}>
                {child.label}
              </Link>
            ))}
        </Stack>
      </Collapse>
    </Stack>
  );
};
export default Navbar

const NAV_ITEMS = [
  {
    label: 'About',
    href: '#about',
    clss:'nav-link about'
  },
  {
    label: 'Skills',
    href: '#skills',
     clss:'nav-link skills'
  },
  {
    label: 'Project',
    href: '#projects',
     clss:'nav-link projects'
  },
  {
    label: 'Contact',
    href: '#contact',
     clss:'nav-link contact'
  },
];
