
import {createContext,useState} from 'react'

export let Context=createContext()

const ContextProvider=({children})=>{
    let [isValue,SetValue]=useState('dark')

    function darkModeFun(){
        if(isValue==='light'){
            SetValue('dark')
        }else{
            SetValue('light')
        }
    }
    return <Context.Provider value={{isValue,darkModeFun}}>
        {children}
    </Context.Provider>
}

export default ContextProvider